@media screen and (max-width: 768px) {
  .burger_menu {
    cursor: pointer;
  }

  .burger_menu div {
    width: 30px;
    height: 5px;
    -o-transition: all .3s ease-in-out;
    background: #fff;
    border-radius: 4px;
    margin: 5px;
    transition: all .3s ease-in-out;
  }

  .nav_list {
    width: 100%;
    transform-origin: top;
    -o-transition: .3s ease-in-out;
    background: #393939;
    flex-direction: column;
    align-items: center;
    padding-bottom: 40px;
    transition: all .3s ease-in-out;
    position: fixed;
    top: 8vh;
    transform: scaleY(0);
  }
}

.switch_menu div {
  background: #18cdca;
}

.switch_menu .one {
  transform: rotate(45deg)translate(6px, 8px);
}

.switch_menu .two {
  opacity: 0;
}

.switch_menu .three {
  transform: rotate(-45deg)translate(6px, -8px);
}

.toggle_navigation {
  transform: scaleY(1);
